import React, { Component } from 'react';
import styled, { css } from 'styled-components';
import { FontAwesomeIcon } from '@fortawesome/react-fontawesome';
import { faPlusCircle, faTrashAlt, faClock, faSave } from '@fortawesome/free-solid-svg-icons'
import TimeItem from './elements/TimeItem';
import Modal from "react-modal";

const customStyles = {
  content : {
    backgroundColor       : '#001f3f',
    top                   : '50%',
    left                  : '50%',
    right                 : 'auto',
    bottom                : 'auto',
    marginRight           : '-50%',
    transform             : 'translate(-50%, -50%)'
  }
};

Modal.setAppElement('#root');

const Wrapper = styled.div`
  max-width: 1200px;
  padding: 20px;
  margin: auto;
  display: flex;
  flex-direction: column;
  font-family: 'Lato', sans-serif;
`;
const Header = styled.div`
  display: flex;
  align-items: center;
`;
const Button = styled.div`
  color: #ffffff;
  text-transform: uppercase;
  box-sizing: border-box;
  cursor: pointer;
  opacity: 0.8;
  transition: 0.5s;
  &:hover{
    opacity: 1;
  }
  svg {
    width: 30px!important;
    height: 30px!important;
  }
`;
const SpentTime = styled.div`
  margin-left: auto;
  color: #ffffff;
  display: flex;
  align-items: center;
  svg{
    margin-right: 20px;
    width: 30px!important;
    height: 30px!important;
  }
`;

const Content = styled.div`
  display: block;
  width: 100%;
  max-height: calc(100vh -100px);
  overflow-y: auto;
  border: 1px solid #ffffff;
  padding: 0 20px;
  box-sizing: border-box;
  margin-top: 30px;
  &>div{
    &:last-child{
      border-bottom: 0;
    }
  }
`;
const InputWrap = styled.div`
  display: flex;
  justify-content: space-between;
`;
const Input = styled.div(props => {
  return css`
    width: calc(50% - 10px);
    position: relative;
    &:after{
      content: '${props.type}';
      position: absolute;
      width: 40px;
      right: 1px;
      top: 1px;
      bottom: 1px;
      background-color: #001f3f;
      display: flex;
      align-items: center;
      justify-content: center;
      font-family: 'Lato', sans-serif;
      color: #ffffff;
    }
    input{
      box-sizing: border-box;
      border: 1px solid transparent;
      border-bottom-color: #ffffff;
      background-color: transparent;
      font-size: 20px;
      padding: 5px 25px 5px 10px;
      width: 100%;
      color: #ffffff;
      &:focus{
        outline: none;
        border-color: #ffffff;
      }
    }
  `;
});

class TimeCalculator extends Component {
  constructor(props){
    super(props);
    let timeItems = localStorage.getItem('timeCalculator');
    if(timeItems){
      timeItems = timeItems.split(',');
    }
    else{
      timeItems = [];
    }
    this.state = {
      spentTime: 0,
      timeItems: timeItems,
      modalIsOpen: false,
      hours: null,
      minutes: null,
      inputRef: React.createRef()
    };
    this.openModal = this.openModal.bind(this);
    this.closeModal = this.closeModal.bind(this);
    this.handleChangeHours = this.handleChangeHours.bind(this);
    this.handleChangeMinutes = this.handleChangeMinutes.bind(this);
    this.onKeyPressed = this.onKeyPressed.bind(this);
    this.saveToLocal = this.saveToLocal.bind(this);
    this.removeLocalData = this.removeLocalData.bind(this);
  }
  openModal() {
    this.setState({modalIsOpen: true});
  }
  saveToLocal() {
    const { timeItems } = this.state;
    localStorage.setItem('timeCalculator', timeItems);
  }
  removeLocalData() {
    localStorage.removeItem('timeCalculator');
    this.setState({
      timeItems: []
    })
  }
  closeModal() {
    this.setState({modalIsOpen: false});
  }
  onKeyPressed(e){
    const { modalIsOpen, hours, minutes, inputRef } = this.state;
    const { keyCode } = e;
    if(keyCode === 13){
      if (modalIsOpen){
        let { timeItems } = this.state;
        let spentTime = 0;
        const timeItem = (+hours * 60) + (+minutes);
        if (timeItem !== 0){
          timeItems.push(timeItem);
          timeItems.forEach( item => {
            spentTime += item;
          });
          this.setState({
            timeItems,
            spentTime,

          });
        }
        this.setState({
          hours: null,
          minutes: null
        });
        inputRef.current.focus();
      }
      else{

      }
    }

  }
  handleChangeHours(e){
    this.setState({
      hours: e.target.value
    });
  }
  handleChangeMinutes(e){
    this.setState({
      minutes: e.target.value
    });
  }
  render() {
    const { spentTime, timeItems, hours, minutes } = this.state;
    const spentHours = Math.floor(spentTime / 60);
    const spentMinutes = Math.floor(spentTime % 60);
    return (
      <Wrapper onKeyDown={this.onKeyPressed}>
        <Header>
          <Button onClick={this.openModal}><FontAwesomeIcon icon={ faPlusCircle } /></Button>
          <Button onClick={this.saveToLocal} style={{marginLeft: '20px'}}><FontAwesomeIcon icon={ faSave } /></Button>
          <Button onClick={this.removeLocalData} style={{marginLeft: '20px'}}><FontAwesomeIcon icon={ faTrashAlt } /></Button>
          <SpentTime>
            <FontAwesomeIcon icon={ faClock} />
            {spentHours !== 0 ? spentHours + ' h' : ''} {spentMinutes !== 0 ? spentMinutes + ' m' : ''}
          </SpentTime>
        </Header>
        {timeItems.length !== 0 && (
          <Content>
            {timeItems.map( (time, index) => (
              <TimeItem time={time} id={index + 1} key={time.toString() + '_' + index.toString()} />
            ))}
          </Content>
        )}
        <Modal
          isOpen={this.state.modalIsOpen}
          onRequestClose={this.closeModal}
          style={customStyles}
          contentLabel="Set Time"
        >
          <InputWrap>
            <Input type="h">
              <input autoFocus ref={this.state.inputRef} onChange={this.handleChangeHours} type="number" placeholder="Hours" value={hours ? hours : ''} />
            </Input>
            <Input type="m">
              <input onChange={this.handleChangeMinutes} type="number" placeholder="Minutes"  value={minutes ? minutes : ''}/>
            </Input>
          </InputWrap>
        </Modal>
      </Wrapper>
    );
  }
}

export default TimeCalculator;
