import React from 'react';
import styled from "styled-components";

const Wrapper = styled.div`
  display: flex;
  width: 100%;
  height: 50px;
  align-items: center;
  justify-content: flex-start;
  color: #ffffff;
  border-bottom: 1px solid #ffffff;
`;
const Id = styled.span`
  min-width: 30px;
  max-width: 30px;
  min-height: 30px;
  max-height: 30px;
  display: flex;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  border: 2px solid #ffffff;
  border-radius: 50%;
  margin-right: 10px;
`;
const Time = styled.div`
  font-size: 20px;
`;
const TimeItem = ({ id, time, onClick }) => {
  const hours = Math.floor(time / 60);
  const minites = Math.floor(time % 60);
  return(
    <Wrapper>
      <Id>{id}</Id>
      <Time>{hours !== 0 ? hours + ' h' : null} {minites !== 0 ? minites + ' m' : null}</Time>
    </Wrapper>
  )
};

export default TimeItem;
